.bounce-enter-active {
    -webkit-animation: bounce-in .8s;
            animation: bounce-in .8s;
}
.bounce-leave-active {
    -webkit-animation: bounce-out .4s;
            animation: bounce-out .4s;
}
@-webkit-keyframes bounce-in {
0% {
        -webkit-transform: scale(0);
                transform: scale(0);
}
50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
}
100% {
        -webkit-transform: scale(1);
                transform: scale(1);
}
}
@keyframes bounce-in {
0% {
        -webkit-transform: scale(0);
                transform: scale(0);
}
50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
}
100% {
        -webkit-transform: scale(1);
                transform: scale(1);
}
}
@-webkit-keyframes bounce-out {
0% {
        -webkit-transform: scale(1);
                transform: scale(1);
}
50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
}
100% {
        -webkit-transform: scale(0);
                transform: scale(0);
}
}
@keyframes bounce-out {
0% {
        -webkit-transform: scale(1);
                transform: scale(1);
}
50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
}
100% {
        -webkit-transform: scale(0);
                transform: scale(0);
}
}



